import React from 'react';
import './Card.scss'
import {Tooltip} from '../Tooltip/Tooltip';
import {CheckingLink} from "../../../generic/Function/CheckingLink";
import {observer} from "mobx-react-lite";
import mainStateStore from "../../../lib/store/main-state-store";
import {Loan} from "../../../lib/models/loan";
import {FormatNumberWithCommas} from "../../../../core/Function/FormatCurrency";


interface ICard {
    data: Loan
}

export const Card = observer(({data}: ICard) => {
    const desc = [
        {
            title: "Акция",
            value: data.desc.stocks.length === 0 ? 'Первый займ под 0 %' : data.desc.stocks,
            icon: "fa-solid fa-gift mr-4",
        },
        {
            title: "Сумма выдачи ОТ и ДО",
            value: `${FormatNumberWithCommas(data.desc.summa_from)} - ${FormatNumberWithCommas(data.desc.summa_before)} ₽`,
            icon: "fa-solid fa-wallet mr-4",
        },
        {
            title: "Процентная ставка",
            value: data.desc.percent,
            icon: "fa-solid fa-percent mr-6",
        },
    ]
    return (
        <div className="card card-body">
            <div className='card__title'>
                <div className='flex align-middle items-center'>
                    <img className='card__img'
                         src={data.img}
                         alt="Shoes"/>
                    <div>
                        <div className='card__title'>
                            <div className='card__name'>
                                {data.title}
                            </div>
                            {
                                data.tooltip.map((tool, index) => (
                                    index === 0 &&
                                    <Tooltip key={`${tool.title}-${index}`}
                                             title={<i className={`${tool.icon} mr-2`} style={{color: tool.color}}/>}
                                             description={tool.desc}/>
                                ))
                            }
                        </div>
                        {
                            data.gosuslugi && <Tooltip
                                description={'+40% к одобрению'}
                                title={<img className='card__img-2'
                                            src={'/gos.png'}
                                            alt="Shoes"/>}
                            />
                        }
                    </div>
                </div>

                <div className='card__rate'
                     style={{color: data.rate.value > 9 ? "#19AB4A" : data.rate.value < 9 && data.rate.value > 6 ? "#FFCC00" : data.rate.value > 6 ? "#FC3F1D" : "#FC3F1D"}}>
                    {data.rate.value}
                </div>

            </div>
            <div className='card__desc'>
                {
                    desc.map((descItem, index) => (
                        <Tooltip className='card__item bg-base-200' key={`${descItem.title}-${index}`} title={
                            <>
                                <i className={descItem.icon}/>
                                {descItem.value}
                            </>
                        } description={descItem.title}/>
                    ))
                }
            </div>
            <button className="btn card__btn" onClick={() => {
                mainStateStore.setModalState(true)
                CheckingLink(data.link)
            }}>Оформить
            </button>
        </div>
    );
})
